import { Fighter, IFighter } from './fighter';

export interface IFight {
  fighter1: IFighter;
  fighter2: IFighter;
}

export class Fight implements IFight{
  fighter1: IFighter;
  fighter2: IFighter;

  initializeFight() {
		const setInfoButton1 = document.getElementById('challenge1') as HTMLButtonElement;
		const setInfoButton2 = document.getElementById('challenge2') as HTMLButtonElement;
		const startButton = document.getElementById('start') as HTMLButtonElement;
		const resetButton = document.getElementById('reset') as HTMLButtonElement;

		const name1 = document.getElementById("name1") as HTMLInputElement;
		const name2 = document.getElementById("name2") as HTMLInputElement;
		const health1 = document.getElementById("health1") as HTMLInputElement;
		const health2 = document.getElementById("health2") as HTMLInputElement;
		const power1 = document.getElementById("power1") as HTMLInputElement;
		const power2 = document.getElementById("power2") as HTMLInputElement;
		// resetButton.addEventListener('click', () => this._resetRase());
		setInfoButton1.addEventListener('click', () => this.fighter1.setInfo(name1, health1, power1));
		setInfoButton2.addEventListener('click', () => this.fighter2.setInfo(name2, health2, power2));
		
		startButton.addEventListener('click', () => { 
			// this._startFight();
		});
  }

	private _startFight(fighter, improvedFighter, ...point){
		if(!!this.fighter1 && !!this.fighter2) {
			console.log(`The fight between ${improvedFighter.name} and ${fighter.name} begins!`);
		  let round = 0;

		  while (fighter.health > 0 && improvedFighter.health > 0) {
			let pointIndex = Math.floor(Math.random() * point.length);
			console.log(`Round - ${round}\nPoint for current round - ${point[pointIndex]}`);
			fighter.hit(improvedFighter, point[pointIndex]);
			if (improvedFighter.health < 0) {
			  improvedFighter.knockout().then(() => {
					console.log(`${fighter.name} wins!`);
			  });
			  break;
			}

			//roll hit/doubleHit
			let doubleHitChance = Math.random();
			console.log(`${improvedFighter.name} rolled - ${doubleHitChance}`);
			if (doubleHitChance > 0.5) {
			  improvedFighter.doubleHit(fighter, point[pointIndex]);
			} else {
			  improvedFighter.hit(fighter, point[pointIndex]);
			}

			if (fighter.health < 0) {
			  fighter.knockout().then(() => {
					console.log(`${improvedFighter.name} wins!`);
			  });
			  break;
			}

			round++;
			console.log();
		  }
		}
	}

	// private _resetRase(){
		// const $motorcycle = document.getElementById('motorcycle');
		// $motorcycle.style.left = '0px';
	// }
}







// create async function fight
async function fight() {
  
}

export default fight;



